import { useForm } from "react-hook-form";
import * as yup from "yup";
import { Schema } from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
// import "./styles.css";
import { ControlledTextField } from "../../hook-form/ControlledTextField";
import { PrimaryButton } from "office-ui-fabric-react";
import { useEffect, useRef, useState } from "react";
import { getPosts } from "../../components/utils/getData";
import { ResponseAPI } from "../../components/api";

import axios from "axios";
import { nameof } from "../../components/utils/utils";

const iconProps = { iconName: 'Permissions' };
type Form = {
   
  llave: number;
  };
  type ValidationContext = {
    shouldValidate: () => boolean;
  };

export default function GenerarLLave() {
  

    const shouldValidate = useRef(false);

    const [posts, setPosts] = useState<ResponseAPI[]>([])

    const schema: Schema<Form> = yup.object().shape({
      

      llave: yup
      .number()
      .typeError('Se requiere un número')
      .required("Este campo es requerido")
      .min(0, "El mínimo es 0")
      .max(10000000000, "Superó los 10 digitos"),
    });
    const { handleSubmit, control, setValue } = useForm<Form, any>({
      defaultValues: {
       
        llave: 0
      },
      resolver: yupResolver(schema),
      mode: "all",
      context: {
        shouldValidate: () => shouldValidate.current
      } as ValidationContext
    });
    const onSave = () => {
       
       
      handleSubmit(
        (data) => {
            console.log(data);
            
            // 1
            getPosts(data).then(data =>{
                // console.log(data);
                
                setPosts(data)
            } 
               
                
            )
           
          
        },
        (err) => {
          console.log(err);
        }
      )();
    };
    return (
        
            <div className="App">
        <h1 style={{ textAlign: "center" }}>
          GENERADOR
        </h1>
        <ControlledTextField
          label="Ingresa el Serial"
          control={control}
          name={nameof<Form>("llave")}
          type="number" 
          minMaxNumber={0}
          iconProps={iconProps}
          // Permissions  
                    />
        <div style={{ margin: "10px 0" }}>
          <PrimaryButton onClick={onSave} text="Generar" />
           {posts.map(post => (
                       <div key={post.id}>
                            <p>LLAVE: <span>{post.data}</span></p>
                        </div>
                            
                           
                        
                    ))}
          
         
        </div>
      </div>
      
      
    );
  }
  