import { Stack, Text, Link } from "@fluentui/react";
import React from "react";



export default function Sisasv1() {
    const stackStyles = {
        root: {
          alignItems: 'center',
          paddingTop: 10,
          
          display: 'flex',
          height: 199,
          justifyContent: 'center',
         
          
        },
      };
      const itemStyles = {
        alignItems: 'center',
        display: 'block',
       
        justifyContent: 'center',
        
        width: '100%',
      };


      const tokens = {
        sectionStack: {
          childrenGap: 5,
        },
        headingStack: {
          childrenGap: 5, //separacion de text
        },
      };

      const contenido = {
        // padding: '0px 160px 0px 160px'
      }
    return (
        <div>
        <Stack horizontal styles={stackStyles}>
            <Text variant='xxLarge' >
                <span style={itemStyles}>
                Sistema de Asistenci - V1
                
                </span>
                
            </Text>
        
        </Stack>

    <Stack tokens={tokens.sectionStack} style={contenido}>
        <Stack tokens={tokens.headingStack}>
        <Text variant={'large'} block>
            Wrap (Default)
        </Text>
        <Text>
            Lorem ipsum {' '}
        <Link href="https://developer.microsoft.com/en-us/fluentui#/controls/web/link" underline>
          este es el link.
        </Link>{' '}dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore
            magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
            consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
            pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est
            laborum.

            
        </Text>
        </Stack>
        
    </Stack>
    
        </div>
    )
}