import { Stack, Text } from "@fluentui/react";
import React from "react";

export default function Blog() {
    const stackStyles = {
        root: {
          alignItems: 'center',
          paddingTop: 100,
          
          display: 'flex',
          height: 199,
          justifyContent: 'center',
         
          
        },
      };
      const itemStyles = {
        alignItems: 'center',
        display: 'block',
       
        justifyContent: 'center',
        
        width: '100%',
      };
    return (
        <div>
        <Stack horizontal styles={stackStyles}>
            <Text variant='mega' >
                <span style={itemStyles}>
                Blog
                
                </span>
                
            </Text>
        
        </Stack>
        </div>
    )
}