import { DocumentCard, DocumentCardActivity, DocumentCardDetails, DocumentCardImage, DocumentCardTitle, Icon, ImageFit, Separator, Stack } from "@fluentui/react";
import React from "react";
import { Text, ITextProps } from '@fluentui/react/lib/Text';
import { useNavigate } from "react-router-dom";
import assisv1 from "../img/assisv1.png";
import FASTPLE3 from "../img/FASTPLE3.5.png";
 export default function Home (){
    const navigate = useNavigate();
    const stackStyles = {
        root: {
          alignItems: 'center',
          paddingTop: 10,
          
          display: 'flex',
          height: 199,
          justifyContent: 'center',
         
          
        },
      };
      const itemStyles = {
        alignItems: 'center',
        display: 'block',
       
        justifyContent: 'center',
        
        width: '100%',
      };
      const iconStyles = {
        root: {
        fontSize: '24px',
        height: '24px',
        width: '24px',
        },
    };
    const cardStyles = {
        root: { display: 'inline-block', marginRight: 20, marginBottom: 20, width: 320 },
      };
      const people = [
        { name: 'ScriptNet', profileImageSrc: '', initials: 'SN' },
      ]
    return (
        <><div>
        <Stack horizontal styles={stackStyles}>
         <Text variant='mega' >
            <span style={itemStyles}>
            ScriptNet
            
            </span>
            
        </Text>
        
        </Stack>
        <Separator>
          <Icon iconName="AssessmentGroup" styles={iconStyles} />
        </Separator>
        </div><DocumentCard
            aria-label={'Document Card with image. How to make a good design. ' +
                'Last modified by Annie Lindqvist and 2 others in March 13, 2018.'}
            styles={cardStyles}
            onClick={() =>{
                navigate('/sisav1')
            }}
           
        >
            
                <DocumentCardImage height={150} imageFit={ImageFit.cover} 
                imageSrc={assisv1} />
                <DocumentCardDetails>
                    <DocumentCardTitle title="Sistema de Asistencia V1" shouldTruncate />
                </DocumentCardDetails>
                <DocumentCardActivity activity="Modificado Febrero 16, 2023" people={[people[0]]} />
            </DocumentCard>
            {/* fast ple */}
            <DocumentCard
            aria-label={'Document Card with image. How to make a good design. ' +
                'Last modified by Annie Lindqvist and 2 others in March 13, 2018.'}
            styles={cardStyles}
            onClick={() =>{
                navigate('/genfastple')
            }}
           
        >
            
                <DocumentCardImage height={150} imageFit={ImageFit.cover} 
                imageSrc={FASTPLE3} />
                <DocumentCardDetails>
                    <DocumentCardTitle title="Generar Serial | FastPLE 3.5" shouldTruncate />
                </DocumentCardDetails>
                <DocumentCardActivity activity="Modificado Febrero 24, 2023" people={[people[0]]} />
            </DocumentCard></>
    )
}