import * as React from 'react';
import { CommandBar, ICommandBarItemProps } from '@fluentui/react/lib/CommandBar';
import { IButtonProps } from '@fluentui/react/lib/Button';
import { Layer } from '@fluentui/react/lib/Layer';
import { Panel } from '@fluentui/react/lib/Panel';
import { DefaultButton, PrimaryButton } from '@fluentui/react/lib/Button';
import { useBoolean } from '@fluentui/react-hooks';
import { PanelContent } from "./ContentPanel";
import { useParams, useNavigate } from 'react-router-dom';
const overflowProps: IButtonProps = { ariaLabel: 'More commands' };
const buttonStyles = { root: { marginRight: 8 } };


export const CommandBarBasicExample: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [isOpen, { setTrue: openPanel, setFalse: dismissPanel }] = useBoolean(false);
  const onRenderFooterContent = React.useCallback(
    () => (
      <div>
        {/* <PrimaryButton onClick={dismissPanel} styles={buttonStyles}>
          Save
        </PrimaryButton> */}
        <DefaultButton onClick={dismissPanel}>Cerrar</DefaultButton>
      </div>
    ),
    [dismissPanel],
  );
  const _items: ICommandBarItemProps[] = [
    {
      key: 'newItem',
      text: 'Inicio',
      cacheKey: 'myCacheKey', // changing this key will invalidate this item's cache
      iconProps: { iconName: 'Home' },
      onClick:()=>(
        navigate('/')
      )
      
    },
    // {
    //   key: 'share',
    //   text: 'Blog',
    //   iconProps: { iconName: 'Blog' },
    //   onClick:()=>(
    //     navigate('/blog')
    //   )
    // },
    // {
    //   key: 'upload',
    //   text: 'Contáctame',
    //   onClick: openPanel,
    //   // target: 'https://web.whatsapp.com/send?phone=51920661504&text=Hola+ScriptNet!',
    //   // href: 'https://web.whatsapp.com/send?phone=51920661504&text=Hola+ScriptNet!',
  
    //   iconProps: { iconName: 'Phone' },
      
    // },
  ];

  return (
   
    
 
    <Layer>
      <Panel
        isLightDismiss
        isOpen={isOpen}
        onDismiss={dismissPanel}
        headerText="Contáctame - Encontrarás mis Redes Sociales"
        closeButtonAriaLabel="Close"
        
        onRenderFooterContent={onRenderFooterContent}
        isFooterAtBottom={true}
      >
        <div>
          <PanelContent />
          <p>(Check the debug console for results after dismissing the panel.)</p>
        </div>
      </Panel>
        <CommandBar
          items={_items}
          // overflowItems={_overflowItems}
          overflowButtonProps={overflowProps}
          farItems={_farItems}
          ariaLabel="Inbox actions"
          primaryGroupAriaLabel="Email actions"
          farItemsGroupAriaLabel="More actions" />
      </Layer>
     
      
  );
};



const _overflowItems: ICommandBarItemProps[] = [
  { key: 'move', text: 'Descargar CV', onClick: () => console.log('Move to'), iconProps: { iconName: 'Download' } },
];

const _farItems: ICommandBarItemProps[] = [
  {
    key: 'farItem1',
    text: 'Contáctame',
    iconProps: { iconName: 'Phone' },
   
    subMenuProps: {
      items: [
        {
          key: 'emailMessage',
          text: 'E-MAIL',
          iconProps: { iconName: 'Mail' },
          subMenuProps: {
            items: [
              {
                key: 'testButtonS1',
                text: 'Outlook',
                href: 'http://www.bing.com',
                target: '_blank',
              },
              {
                key: 'testButtonS2',
                text: 'Gmail',
                href: 'http://www.bing.com',
                target: '_blank',
              },
            ],
          },
        },
        {
          key: 'calendarEvent',
          text: 'Redes Sociales',
          iconProps: { iconName: 'Chat' },
          
          subMenuProps: {
            items: [
              {
                key: 'testButton',
                text: 'Facebook',
                
              },
              {
                key: 'testButton2',
                text: 'whatsApp',
              
                href: 'http://www.bing.com',
                target: '_blank',
              },
            ],
          },
        },
        {
          key: 'CVDO',
          text: 'Descargar CV',
          iconProps: { iconName: 'Download' },
          
          
        },
        {
          key: 'splitButtonTest',
          text: 'Otros...',
          split: true,
         
          subMenuProps: {
            items: [
              {
                key: 'splitButtonSubMenu1',
                text: 'Descargar CV',
              },
              {
                key: 'splitButtonSubMenu2',
                text: 'Submenu Item w/o Keytip',
              },
            ],
          },
        },
      ],
    },
  },
];
