import React, { useEffect, useState } from 'react';
import './Visialisation.css';
import {
  FontWeights,
  getTheme,
  IconButton,
  mergeStyleSets,
  Modal,
  PrimaryButton,
  Separator,
  Text,
  Stack, IStackStyles, IStackTokens,
} from '@fluentui/react';
import { Icon } from '@fluentui/react/lib/Icon';
// Styles STACK
const stackStyles = {
  root: {
    alignItems: 'center',

    display: 'flex',
    height: 100,
    justifyContent: 'center',
    
  },
};
const customSpacingStackTokens = {
  childrenGap: 5,
  padding: 10,
};
const itemStyles = {
  alignItems: 'center',
  display: 'block',
  height: 50,
  justifyContent: 'center',
  textAlign: 'center',
  width: 100,
};
// ICONOS STILO
const iconStyles = {
  root: {
    fontSize: '24px',
    height: '24px',
    width: '24px',
  },
};
const initialOpenState = false;

const theme = getTheme();
const cancelIcon = { iconName: 'Cancel' };
const iconButtonStyles = {
  root: {
    color: theme.palette.neutralPrimary,
    marginLeft: 'auto',
    marginTop: '4px',
    marginRight: '2px',
  },
  rootHovered: {
    color: theme.palette.neutralDark,
  },
};
const contentStyles = mergeStyleSets({
  container: {
    display: 'flex',
    flexFlow: 'column nowrap',
    alignItems: 'stretch',
  },
  header: [
    theme.fonts.xLargePlus,
    {
      flex: '1 1 auto',
      borderTop: `4px solid ${theme.palette.themePrimary}`,
      color: theme.palette.neutralPrimary,
      display: 'flex',
      alignItems: 'center',
      fontWeight: FontWeights.semibold,
      padding: '12px 12px 14px 24px',
    },
  ],
  body: {
    flex: '4 4 auto',
    padding: '0 24px 24px 24px',
    overflowY: 'hidden',
    selectors: {
      p: { margin: '14px 0' },
      'p:first-child': { marginTop: 0 },
      'p:last-child': { marginBottom: 0 },
    },
  },
});
const proyectos = 'Proyectos';
const WelcomeNotice = () => {
  const [isOpen, setIsOpen] = useState(initialOpenState);

  useEffect(() => {
    clearTimeout(window.welcomeNoticeTimeout);
    window.welcomeNoticeTimeout = setTimeout(() => {
      setIsOpen(true);
    }, 1000);
    return () => {
      clearTimeout(window.welcomeNoticeTimeout);
    };
  }, []);

  const handleDismiss = () => setIsOpen(false);

  return (
    <Modal
      isOpen={isOpen}
      onDismiss={handleDismiss}
      isBlocking={false}
      containerClassName="launch-modal"
    >
      <div className={contentStyles.header}>
        <span>Bienvenido</span>
        <IconButton
          styles={iconButtonStyles}
          iconProps={cancelIcon}
          ariaLabel="Close welcome"
          onClick={handleDismiss}
        />
      </div>
      <div className={contentStyles.body}>
      
        <div>
        <Text>
        Hola me llamo Alex, Desarrollador de Aplicaciones Web, Móviles y de Escritorio. Con experiencia en herramientas y procedimientos de desarrollo más innovadoras.
        </Text>      
        <Separator>
          <Icon iconName="Clock" styles={iconStyles} />
        </Separator>
        </div>
        <Stack enableScopedSelectors>
         
          <Stack enableScopedSelectors horizontal styles={stackStyles} tokens={customSpacingStackTokens}>
            <span style={itemStyles}>
              +0
              <p>Web</p>
            </span>
            
            <span style={itemStyles}>
              +0
              <p>Moviles</p>
            </span>
            <span style={itemStyles}>
              +0
              <p>Escritorio</p>
            </span>
          </Stack>
        </Stack>
        
        
        <PrimaryButton
          onClick={handleDismiss}
        >Continuar</PrimaryButton>
      </div>
    </Modal>
  );
};

export default WelcomeNotice;
